html {
  --main-color: #00acdc;
  --text-accent-color: #060b35;
  --text-secondary-color: #616070;
  --white-color: #fff;
  color: var(--text-secondary-color);
  font-family: Roboto, sans-serif;
  font-size: 10px;
  line-height: 14px;
}

.card {
  box-sizing: border-box;
  background: var(--white-color);
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: var(--text-accent-color);
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  margin-top: 0;
  margin-bottom: 7px;
}

.card__review-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__review-count {
  color: var(--text-accent-color);
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__price-label {
  font-size: 12px;
  line-height: 18px;
}

.card__price {
  color: var(--text-accent-color);
  font-size: 16px;
  font-weight: bold;
  line-height: 18px;
}

.card__buy {
  box-sizing: border-box;
  border: 1px solid var(--main-color);
  color: #fff;
  text-transform: uppercase;
  background-color: var(--main-color);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: var(--main-color);
  background-color: var(--white-color);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__image {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  background-color: #fff;
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

body {
  margin: 0;
}

.grid {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 488px) {
  .grid {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .grid {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .grid {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .grid {
    grid-template-columns: repeat(4, 200px);
  }
}
